  display: flex;
  flex-direction: column;
  align-items: center;
}
/* SVG */
.generatingAnim{
  position: fixed;
  top: 30vh;
  width: 15vw;
  height: 15vw;
}
/* ROWS */
.row{
  display: flex;
  flex-direction: row;
  align-items: center;
}
/* UPLOAD CONTAINER */
.upload_container{
  border-style: dashed;
  border-radius: 5px;
  font-size: 2em;
}
.file-wrapper {
  text-align: center;
  width: 350px;
  height: 350px;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  overflow: hidden;
}
.file-wrapper input {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    opacity: 0.0;
    filter: alpha(opacity=0);
    font-size: 300px;
    height: 100%;
}
/* CANVAS */
.canvas_preview{
  width: 350px;
  height: 350px;
  border-style: dashed;
  border-radius: 5px;
}
.canvas_image{
  display: none;
}

/* BUTTONS */
.clearButton{
  position: relative;
  top: 10px;
  left: -300px;
  font-weight: bold;
  padding: 10px;
  color: red;
  background-color: transparent;
  border-style: solid;
  border-color: red;
  border-radius: 10px;
}
.clearButton:hover{
  background-color: #e57e7e;
  cursor: pointer;
}
.clearButton:active, .clearButton:focus {
  outline: 0;
  /* border: none; */
  -moz-outline-style: none;
}
.clearButton:disabled {
  pointer-events: none;
  cursor: default;
  opacity: 0.4;
}
.generateButton{
  width: 200px;
  height: 350px;
  font-weight: bold;
  color: #42b983;
  background-color: transparent;
  border-style: dashed;
  border-color: #42b983;
  border-radius: 10px;
  font-size: 25px;
}
.generateButton:hover{
  background-color: #8dd5b4;
  cursor: pointer;
  color: white;
}
.generateButton:active, .generateButton:focus {
  outline: 0;
  /* border: none; */
  -moz-outline-style: none; 
}
.generateButton:disabled {
  border-color: red;
  color: red;
  pointer-events: none;
  cursor: default;
}
.advancedButton{
  font-weight: bold;
