  color: black;
  background-color: transparent;
  border-style: solid;
  border-color: black;
  border-radius: 10px;
  margin: 10px;
}
.advancedButton:hover{
  background-color: gray;
  cursor: pointer;
}
.advancedButton:active, .advancedButton:focus {
  outline: 0;
  /* border: none; */
